import { Box, Divider, Tabs, Tab } from '@mui/material'
import { HomeRounded, FaceRounded, TextSnippetRounded, TerminalRounded, MessageRounded, ConnectWithoutContactRounded } from '@mui/icons-material'
import SidebarHeader from './sidebar/SidebarHeader'
import SidebarFooter from './sidebar/SidebarFooter'

const DrawerContent = ({ value, handleChange, setDrawerOpen }) => {
  const tabProps = (index) => {
    return {
      id: `sidebar-tab-${index}`,
      'aria-controls': `tabpanel-${index}`,
    }
  }
  const drawerHandle = () => {
    setDrawerOpen(false)
  }
  return (
    <Box sx={{ textAlign: 'center', justifiyContent: 'center', mt: 2 }}>
      <SidebarHeader />
      <Divider variant="middle" sx={{ mt: 2 }} />
      <Tabs
        orientation="vertical"
        variant="scrollable"
        scrollButtons="auto"
        allowScrollButtonsMobile
        value={value}
        onChange={handleChange}
        textColor="secondary"
        indicatorColor="secondary"
        sx={{
          '& .MuiTabs-flexContainer': {
            alignItems: 'start',
            px: 4,
          },
        }}
      >
        <Tab label="صفحه اصلی" icon={<HomeRounded />} iconPosition="start" {...tabProps(0)} sx={{ minHeight: 45 }} onClick={drawerHandle} />
        <Tab label="درباره من" icon={<FaceRounded />} iconPosition="start" {...tabProps(1)} sx={{ minHeight: 45 }} onClick={drawerHandle} />
        <Tab label="رزومه من" icon={<TextSnippetRounded />} iconPosition="start" {...tabProps(2)} sx={{ minHeight: 45 }} onClick={drawerHandle} />
        <Tab label="نمونه کارها" icon={<TerminalRounded />} iconPosition="start" {...tabProps(3)} sx={{ minHeight: 45 }} onClick={drawerHandle} />
        <Tab label="نظرات مشتریان" icon={<MessageRounded />} iconPosition="start" {...tabProps(4)} sx={{ minHeight: 45 }} onClick={drawerHandle} />
        <Tab label="ارتباط با من" icon={<ConnectWithoutContactRounded />} iconPosition="start" {...tabProps(5)} sx={{ minHeight: 45 }} onClick={drawerHandle} />
      </Tabs>
      <Divider variant="middle" sx={{ mt: 2 }} />
      <SidebarFooter />
    </Box>
  )
}

export default DrawerContent
